import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import moment from 'moment';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import {
  ColumnDisplayType,
  CreateTenant,
  DateTime,
  EnumDataSource,
  FieldDefinition,
  FixedDataSource,
  ICombinedColumn,
  IFieldOptions,
  InvokeAction,
  LicenseType,
  MetaDataType,
  PageCountLicense,
  PageCountType,
  RolePermissions,
  Tab,
  TableItem,
  Tenant,
  TenantType,
} from 'shared-code-lib';
import { Package } from '../../blocks/models/package';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { UserStateService } from '../../blocks/services/user-state.service';

@Component({
  selector: 'app-admin-tenant',
  templateUrl: './admin-tenant.component.html',
  styleUrls: ['./admin-tenant.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminTenantComponent implements OnInit, OnDestroy {
  tenant: Tenant;

  editable: boolean;

  activeTab = 'packages';
  tabs = new Array<Tab>();

  allPackages = new Array<Package>();
  currentPackages = new Array<Package>();

  fieldName: FieldDefinition;
  fieldEmail: FieldDefinition;
  fieldPassword: FieldDefinition;
  fieldTenantCode: FieldDefinition;
  fieldMobileBranding: FieldDefinition;
  fieldDocumentRetentionPeriod: FieldDefinition;
  fieldTenantType: FieldDefinition;
  fieldPartnerTenant: FieldDefinition;
  fieldSessionTimeout: FieldDefinition;

  fieldLicensingPageType: FieldDefinition;
  fieldLicenseType: FieldDefinition;
  fieldLicensingStartDate: FieldDefinition;
  fieldLicensingEndDate: FieldDefinition;
  fieldLicensingPageCount: FieldDefinition;
  fieldLicensingCustomPageCount: FieldDefinition;

  normalTenantType = TenantType.normal;

  partnersDataSourceFilled = false;
  partnerTenants: Array<Tenant>;

  pageCountLicense: PageCountLicense;
  readonly pageCountOptions: IFieldOptions[] = [
    { value: '10000', name: '10,000' },
    { value: '25000', name: '25,000' },
    { value: '50000', name: '50,000' },
    { value: '100000', name: '100,000' },
    { value: '250000', name: '250,000' },
    { value: '500000', name: '500,000' },
    { value: '1000000', name: '1,000,000' },
    { value: '2000000', name: '2,000,000' },
    { value: '-1', name: 'Custom' },
  ];
  pageCountListValue: string;
  licenseToUpdate: any;
  rowIndexToUpdate: number;

  administratorEmail: string = '';
  administratorPassword: string = '';
  tenantCode: string = '';
  mobileBanding: string = '';

  routeParams: Params;
  routeData: Data;

  formControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  licensingTableItems: TableItem[] = [];
  licensingColumns: ICombinedColumn[];
  licensingSort: SortDescriptor;

  brandingDataSource: FixedDataSource;
  partnerTenantsDataSource = new FixedDataSource(new Array<IFieldOptions>());

  get tenantCleanupCompletedWorkItemsAfterDays() {
    return `${this.tenant.cleanupCompletedWorkItemsAfterDays}`;
  }

  set tenantCleanupCompletedWorkItemsAfterDays(val: string) {
    this.tenant.cleanupCompletedWorkItemsAfterDays = Number(val);
  }

  get tenantSessionTimeInMinutes() {
    return `${this.tenant.sessionTimeInMinutes}`;
  }

  set tenantSessionTimeInMinutes(val: string) {
    this.tenant.sessionTimeInMinutes = Number(val);
  }

  get tenantTenantType() {
    return `${this.tenant.tenantType}`;
  }

  set tenantTenantType(val: string) {
    this.tenant.tenantType = Number(val);
  }

  get pageCountLicenseCountType() {
    return `${this.pageCountLicense.countType}`;
  }

  set pageCountLicenseCountType(val: string) {
    this.pageCountLicense.countType = Number(val);
  }

  get licenseType() {
    return `${this.pageCountLicense.licenseType}`;
  }

  set licenseType(val: string) {
    this.pageCountLicense.licenseType = Number(val);
  }

  get pageCountLicenseDateTermStartsUTC() {
    return moment(this.pageCountLicense.dateTermStartsUTC).local().format(DateTime.NormalizedDateFormat);
  }

  set pageCountLicenseDateTermStartsUTC(val: string) {
    this.pageCountLicense.dateTermStartsUTC = moment(val, DateTime.NormalizedDateFormat).local().toDate();
  }

  get pageCountLicenseDateTermEndsUTC() {
    return moment(this.pageCountLicense.dateTermEndsUTC).local().format(DateTime.NormalizedDateFormat);
  }

  set pageCountLicenseDateTermEndsUTC(val: string) {
    this.pageCountLicense.dateTermEndsUTC = moment(val, DateTime.NormalizedDateFormat).local().toDate();
  }

  get pageCountLicensePageCountAllowance() {
    return `${this.pageCountLicense.pageCountAllowance}`;
  }

  set pageCountLicensePageCountAllowance(val: string) {
    this.pageCountLicense.pageCountAllowance = Number(val);
  }

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private ngxSmartModalService: NgxSmartModalService,
    private userStateService: UserStateService
  ) {}

  ngOnInit() {
    this.editable = this.userStateService.hasRolePermission(RolePermissions.TENANTS_UPDATE);

    this.createBrandingTypes();
    this.createButtons();
    this.createFields();
    this.createTabs();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;

        if (this.routeData.type == 'properties' || this.routeData.type == 'duplicate') {
          this.hydraApi.getTenant(this.routeParams.userid, this.tenantLoaded, this.tenantLoadFailed);
        } else if (this.routeData.type == 'create') {
          this.tenant = new Tenant();
          this.tenantLoaded(this.tenant);
        }
      })
    );

    this.licensingColumns = this.getColumns();

    const defaultSort: SortDescriptor = {
      field: '1',
      dir: 'asc',
    };
    this.licensingSort = defaultSort;
  }

  createBrandingTypes = (): void => {
    const brandings = new Array<IFieldOptions>();
    brandings.push({ name: this.translateService.instant('admin-tenant.brand-ic'), value: '' });
    brandings.push({ name: this.translateService.instant('admin-tenant.brand-ar'), value: 'ar' });
    brandings.push({ name: this.translateService.instant('admin-tenant.brand-hpcr'), value: 'hpcr' });
    brandings.push({ name: this.translateService.instant('admin-tenant.brand-if'), value: 'if' });
    this.brandingDataSource = new FixedDataSource(brandings);
  };

  createTabs = (): void => {
    this.tabs.push(new Tab('packages', this.translateService.instant('admin-tenant.packages')));
    this.tabs.push(new Tab('pageLicensing', this.translateService.instant('admin-tenant.page-licensing')));
  };

  currentPackagesChanged = (items: Array<Package>): void => {
    this.tenant.packageIds = [];
    for (let item of items) {
      this.tenant.packageIds.push(item.id);
    }
  };

  tenantLoaded = (t: Tenant) => {
    if (this.routeData.type == 'duplicate') {
      t.name = `${this.translateService.instant('Admin.CopyOf')} ${t.name}`;
      t.id = '';
    } else if (this.routeData.type == 'create') {
      t.id = '';
    }

    this.tenant = t;

    this.tenant.pageCountLicenses.forEach((pageLicense) => {
      this.licensingTableItems.push(this.createTableItem(pageLicense));
    });

    this.hydraApi.getPackages(this.packagesLoaded, this.packagedFailedToLoad);

    this.fillPartnerTenantDatasource();
  };

  packagesLoaded = (packages: Array<Package>): void => {
    this.allPackages = packages;

    this.currentPackages = [];

    for (const id of this.tenant.packageIds) {
      const p = this.allPackages.find((i) => i.id == id);
      if (p) this.currentPackages.push(p);
    }
  };

  tenantLoadFailed = () => {
    //todo: toastr
  };

  packagedFailedToLoad = () => {
    //todo: toastr
  };

  validate = (): boolean => {
    return true;
  };

  onSave = () => {
    if (this.validate()) {
      if (this.tenant.id) {
        this.hydraApi.updateTenant(this.tenant, this.tenantSaved, this.userSaveFailed);
      } else {
        this.hydraApi.createTenant(
          new CreateTenant(this.tenant, this.administratorEmail, this.administratorPassword),
          this.tenantSaved,
          this.userSaveFailed
        );
      }
    }
  };

  tenantSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-tenant.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-tenant.validation-errors');
    }
  };

  userSaveFailed = (err: any) => {
    this.toastr.error(this.translateService.instant('admin-tenant.save-failed'));
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaAdministratorApplication/BambaAdministratorApplication-Upland.Bamba.Application.Settings.Tenants'
    ); // todo - handle this "better"
  };

  createFields = (): void => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-tenant.name'),
      placeholder: this.translateService.instant('admin-tenant.name-placeholder'),
      required: true,
    });

    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-user.email'),
      placeholder: this.translateService.instant('admin-user.email-placeholder'),
      required: true,
    });

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('admin-user.password'),
      placeholder: this.translateService.instant('admin-user.password-placeholder'),
      required: true,
    });

    this.fieldTenantCode = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-tenant.tenant-code'),
      placeholder: this.translateService.instant('admin-tenant.tenant-code-placeholder'),
    });

    this.fieldMobileBranding = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.mobile-branding'),
      placeholder: this.translateService.instant('admin-tenant.mobile-branding-placeholder'),
      required: true,
      datasource: this.brandingDataSource,
    });

    this.fieldDocumentRetentionPeriod = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant.document-retention-period'),
      placeholder: this.translateService.instant('admin-tenant.document-retention-period-placeholder'),
      required: true,
    });

    this.fieldLicensingPageType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.page-licensing-type'),
      required: true,
      datasource: new EnumDataSource(PageCountType, 'admin-tenant', this.translateService),
    });

    this.fieldLicenseType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.license-type'),
      required: true,
      datasource: new EnumDataSource(LicenseType, 'admin-tenant.license-types', this.translateService),
    });

    this.fieldLicensingStartDate = new FieldDefinition({
      type: MetaDataType.Date,
      label: this.translateService.instant('admin-tenant.page-licensing-effective-date'),
      placeholder: this.translateService.instant('admin-tenant.page-licensing-effective-date-placeholder'),
      required: true,
    });

    this.fieldLicensingEndDate = new FieldDefinition({
      type: MetaDataType.Date,
      label: this.translateService.instant('admin-tenant.page-licensing-expiration-date'),
      placeholder: this.translateService.instant('admin-tenant.page-licensing-expiration-date-placeholder'),
      required: true,
    });

    this.fieldLicensingPageCount = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.page-licensing-allowance'),
      required: true,
      datasource: new FixedDataSource(this.pageCountOptions),
    });

    this.fieldLicensingCustomPageCount = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant.page-licensing-custom-page-count'),
      placeholder: this.translateService.instant('admin-tenant.page-licensing-custom-page-count-placeholder'),
      required: true,
    });

    this.fieldTenantType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.tenant-type'),
      required: true,
      datasource: new EnumDataSource(TenantType, 'admin-tenant', this.translateService),
    });

    this.fieldPartnerTenant = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant.partner'),
      required: true,
      datasource: this.partnerTenantsDataSource,
    });

    this.fieldSessionTimeout = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-tenant.session-timeout'),
      placeholder: this.translateService.instant('admin-tenant.session-timeout'),
      required: true,
    });

    this.hydraApi.getPartnerTenants(this.partnerTenantsLoaded, this.partnerTenantsLoadFailed);
  };

  partnerTenantsLoaded = (rs: Array<Tenant>): void => {
    this.partnerTenants = rs;
    this.fillPartnerTenantDatasource();
  };

  fillPartnerTenantDatasource = (): void => {
    if (this.partnersDataSourceFilled || !this.partnerTenants || !this.tenant) return;

    const items: Array<IFieldOptions> = this.partnerTenants
      .filter((t) => t.id !== this.tenant.id)
      .map((t) => {
        return { name: t.name, value: t.id };
      });
    items.unshift({ name: this.translateService.instant('none'), value: '' });

    this.partnerTenantsDataSource.updateOptions(items);

    this.partnersDataSourceFilled = true;
  };

  partnerTenantsLoadFailed = (err: any): void => {
    this.toastr.error(this.translateService.instant('admin-tenant.partnerTenantsLoadFailed'));
  };

  createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translateService.instant('admin-tenant.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );
    this.cancelButton = new Control(
      this.onCancel,
      this.translateService.instant(this.editable ? 'admin-tenant.cancel' : 'admin-tenant.close'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    if (this.editable) this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);
  }

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(
      this.createColumn(
        'countType',
        ColumnDisplayType.ActionLink,
        this.translateService.instant('admin-tenant.page-licensing-type'),
        'countTypeText'
      )
    );

    columns.push(
      this.createColumn(
        'licenseType',
        ColumnDisplayType.ActionLink,
        this.translateService.instant('admin-tenant.license-type'),
        'licenseTypeText'
      )
    );

    columns.push(
      this.createColumn(
        'pageCountAllowance',
        ColumnDisplayType.Number,
        this.translateService.instant('admin-tenant.page-licensing-allowance'),
        'pageCountAllowance'
      )
    );

    columns.push(
      this.createColumn(
        'dateTermStartsUTC',
        ColumnDisplayType.Date,
        this.translateService.instant('admin-tenant.page-licensing-effective-date'),
        'dateTermStartsUTC'
      )
    );

    columns.push(
      this.createColumn(
        'dateTermEndsUTC',
        ColumnDisplayType.Date,
        this.translateService.instant('admin-tenant.page-licensing-expiration-date'),
        'dateTermEndsUTC'
      )
    );

    return columns;
  }

  createColumn(id: string, displayType: number, columnName: string, itemProperty: string) {
    return {
      id: 'column_' + id,
      width: 200,
      stretch: false,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  deletePageCountLicense(action: InvokeAction) {
    this.ngxSmartModalService.setModalData(
      {
        title: 'MainHeader.DeleteConfirmation',
        text: this.translateService.instant('admin-tenant.page-licensing-delete-confirm'),
        buttonText: this.translateService.instant('Common.Delete'),
        confirm: () => {
          this.closeConfirmModal();
          this.confirmedDeletePageCountLicense(action.index);
        },
        cancel: this.closeConfirmModal,
      },
      'actionConfirmModal',
      true
    );

    this.ngxSmartModalService.getModal('actionConfirmModal').open();
  }

  closeConfirmModal = () => this.ngxSmartModalService.getModal('actionConfirmModal').close();

  confirmedDeletePageCountLicense = (rowIndex: number) => {
    this.tenant.pageCountLicenses.splice(rowIndex, 1);
    this.licensingTableItems.splice(rowIndex, 1);

    this.refreshLicensingTableItems();
  };

  createTableItem(license: PageCountLicense): TableItem {
    this.setEnumPropertiesText(license);

    const tableItem = new TableItem(license, this.licensingColumns, this.translateService);

    tableItem.data.defaultAction = {
      name: this.translateService.instant('admin-tenant.page-licensing-update'),
    };

    return tableItem;
  }

  showAddLicenseModal() {
    this.pageCountLicense = new PageCountLicense();
    this.pageCountListValue = this.pageCountOptions[0].value;
    this.openPageLicenseModal();
  }

  showUpdateLicenseModal(event: InvokeAction) {
    this.pageCountLicense = new PageCountLicense(event.item);
    this.licenseToUpdate = event.item;
    this.rowIndexToUpdate = event.index;

    if (this.pageCountOptions.some((o) => o.value === `${this.pageCountLicense.pageCountAllowance}`)) {
      this.pageCountListValue = `${this.pageCountLicense.pageCountAllowance}`;
    } else {
      this.pageCountListValue = '-1';
    }

    this.openPageLicenseModal();
  }

  savePageLicense() {
    this.setEnumPropertiesText(this.pageCountLicense);

    if (this.pageCountListValue !== '-1') this.pageCountLicense.pageCountAllowance = Number(this.pageCountListValue);

    if (this.licenseToUpdate) {
      this.tenant.pageCountLicenses[this.rowIndexToUpdate] = this.pageCountLicense;
      this.licensingTableItems[this.rowIndexToUpdate] = this.createTableItem(this.pageCountLicense);

      this.licenseToUpdate = null;
    } else {
      this.tenant.pageCountLicenses.push(this.pageCountLicense);
      this.licensingTableItems.push(this.createTableItem(this.pageCountLicense));
    }

    this.refreshLicensingTableItems();

    this.closePageLicenseModal();
  }

  setEnumPropertiesText(license: PageCountLicense) {
    license['countTypeText'] = this.translateService.instant(`admin-tenant.${PageCountType[license.countType]}`);
    license['licenseTypeText'] = this.translateService.instant(
      `admin-tenant.license-types.${LicenseType[license.licenseType]}`
    );
  }

  refreshLicensingTableItems() {
    this.licensingTableItems = this.licensingTableItems.slice();
  }

  openPageLicenseModal = () => this.ngxSmartModalService.getModal('pageLicenseModal').open();

  closePageLicenseModal = () => {
    this.ngxSmartModalService.getModal('pageLicenseModal').close();
    this.licenseToUpdate = null;
    this.pageCountLicense = null;
  };

  fieldTypeChanged() {
    if (this.tenant.tenantType === TenantType.partner) {
      this.tenant.partnerTenantId = '';
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
